@import url(https://fonts.googleapis.com/css2?family=Bangers);
.background {
  background-image: url(/multiverse-avengers/static/media/space.f0ed3aaa.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  max-width: 100%;
}

.App {
  color: rgb(255, 255, 255);
  text-align: center;
  font-family: 'Bangers', sans-serif;
}

.header {
  background-image: url(/multiverse-avengers/static/media/headerbanner.982da1bf.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
  max-width: 100%;
  margin: 7px;
  padding: 120px;
}
a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.nav-bar {
  background-image: url(/multiverse-avengers/static/media/menuimage.69a3e996.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
  max-width: 100%;
  margin: 8px;
  padding: 1px;
  display: flex;
  justify-content: space-around;
  text-align: center;
  text-shadow: 2px 2px 5px black;
  font-size: 21px;
}

button {
  font-size: 25px;
  background: rgb(238,201,22);
background: linear-gradient(0deg, rgba(238,201,22,0.4640231092436975) 6%, rgba(134,209,246,0.7861519607843137) 100%);
}

img {
  max-width: 30%;
  height: auto;
}

.HeroSelect {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
}

.TheFoot {
  /* background-image: url(/src/images/heroes.jpeg); */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
  max-width: 100%;
  margin: 7px;
  padding: 250px;
  border-radius: 50%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

